@import 'variables';

nav {
  font-size: 0;
  text-align: center;

  a {
    display: inline-block;
    position: relative;
    padding: 8px 15px;
    margin: 7px 15px;
    color: $color-white;
    text-transform: uppercase;
    font-size: 17px;
    font-family: 'Open Sans';
    text-shadow: $medium-shadow;
    letter-spacing: 2px;

    &::after {
      content: '';
      position: absolute;
      top: 100%;
      left: 0;
      width: 0%;
      height: 1px;
      background-color: $color-white;
      box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.4);
      opacity: 0;
      transition: width 0.4s, left 0.4s, opacity 0.3s;
    }

    &:hover::after {
      left: 12.5%;
      width: 75%;
      opacity: 1;
    }

    &.active::after {
      left: 0;
      width: 100%;
      height: 1px;
      opacity: 1;
    }
  }
}
