@mixin linear-gradient($direction, $color-stops...) {
  @if is-direction($direction) ==false {
    $color-stops: $direction, $color-stops;
    $direction: 180deg;
  }

  background: nth(nth($color-stops, 1), 1);
  background: -webkit-linear-gradient(legacy-direction($direction), $color-stops);
  background: linear-gradient($direction, $color-stops);
}

// Colors
$color-white: #fff;
$color-black: #000;
$soft-white: transparentize($color-white, 0.15);
$soft-black: lighten($color-black, 50%);
$medium-black: lighten($color-black, 30%);
$gradient-color-one: #fb5757;
$gradient-color-two: #fc8b5b;
$gradient-color-three: #fdbd5f;
// Shadows
$light-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
$soft-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
$medium-shadow: 1px 1px 2px rgba(0, 0, 0, 0.6);
// Breakpoints
$breakpoint-xs: 400px;
$breakpoint-sm: 768px;
$breakpoint-md: 992px;
$breakpoint-md: 1200px;
// Fonts
@import url('https://fonts.googleapis.com/css?family=Raleway');
